/**
* 获取现金金额 /app/user/prize/cashAmount
* @param {object} data
* @param {string} data.acCode
* @param {string} data.lunaSessionId
* @param {string} data.authCode
* @param {string} data.appId
* @param {object} options
* @returns
*/
export default function queryCashAmount (params: any = {}, options: any = {}) {
  return this.get('/app/user/prize/cashAmount', params, options)
}
