/**
* 扶摇-查询当前用户所拥有的可用积分
* https://yapi.gyjxwh.com/project/284/interface/api/31670
* @ /app/activity/sign/queryCreditBalance
* @param data
* @param data.acCode * 活动id
* @param data.lunaSessionId * 会话id
* @param data.rewardValueType 奖励类型，默认1
* @param options
* @returns result 积余额
*/
export default function queryCreditBalance (params: any = {}, options: any = {}) {
  return this.get('/app/activity/sign/queryCreditBalance', params, options)
}
