/**
 * 事件任务参数 /app/task/event/paramInfo
 * @param {object} data
 * @param {string} data.taskId
 * @param {string} data.acCode
 * @param {string} data.apmbA
 * @param {string} data.lunaSessionId
 * @param {object} options
 * @returns
 */
export default function getEventParamInfo (params: any, options: Object = {}) {
  return this.get('/app/task/event/paramInfo', params, options)
}
