/**
 * 获取邀请任务被邀请人信息 /app/task/getBeInvitedInfo
 * @param {object} data
 * @param {string} data.acCode
 * @param {string} data.taskId
 * @param {object} options
 * @returns
 */
export default function getBeInvitedInfo (params: any, options: Object = {}) {
  return this.get('/app/task/getBeInvitedInfo', params, options)
}
