/**
* 查询用户头像、昵称信息
* @ 使用场景: 查询用户头像、昵称信息.
* @param {object} data
* @param {string} data.appId
* @param {string} data.authCode
* @param {string} data.lunaSessionId
* @param {object} options
* @returns
*/
export default function queryUserShareInfo (params: any = {}, options: any = {}) {
  return this.get('/app/user/query/share-info', params, options)
}
