import CryptoJS from 'crypto-js'
import md5 from 'js-md5'
import { Base64 } from 'js-base64'
/**
 * 定义常量key、iv 此常量后端提供
 */
const _KEV = 'iHATLhQo0zln1508'
const _IV = 'iHATLhQo0zln1508'

/**
 * 定义AES加密方法
 * @param data: 需要加密的js对象 例如{a:2,b:'code'}
 * @returns 返回base64加密后的字符串 后续接口参数content要用到
 */
export const encrypt = (data = {}) => {
  const key = CryptoJS.enc.Utf8.parse(_KEV)
  const iv = CryptoJS.enc.Utf8.parse(_IV)
  const encrypted = CryptoJS.AES.encrypt(JSON.stringify(data), key,
    {
      iv: iv,
      mode: CryptoJS.mode.CBC,
      padding: CryptoJS.pad.ZeroPadding
    })
  return encrypted.toString() // 返回的是base64格式的密文
}

/**
 * 定义AES解密方法
 * @param encrypted: 需要解密的字符串
 * @returns
 */
export const decrypt = (encrypted) => {
  const key = CryptoJS.enc.Utf8.parse(_KEV)
  const iv = CryptoJS.enc.Utf8.parse(_IV)
  const decrypted = CryptoJS.AES.decrypt(encrypted, key,
    {
      iv: iv,
      mode: CryptoJS.mode.CBC,
      padding: CryptoJS.pad.ZeroPadding
    })
  return decrypted.toString(CryptoJS.enc.Utf8)
}

/**
 * 星火md5加密 后续接口签名参数sign需要用到
 * @param content : AES加密后的字符串;
 * @param time 时间戳
 * @returns 返回content+盐(常量)+时间戳拼接起来然后md5转化后的字符串；
 */
export const md5ForXinghuo = (content = '', time: number) : string => {
  const SALT = '3afn4UpdQzENHhZji1jC'// 加盐 此常量后端提供
  const s = content + SALT + time
  return md5(s)
}

// 灯火加密
export const md5ForFyDengHuo = (dataMapString, timestamp, nonce) => {
  const SALT = 'gyjx*&34^)26' // 加盐 此常量后端提供
  const s = Base64.encode(SALT + dataMapString + timestamp + nonce)
  return md5(s)
}

export const getUUid = () => {
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
    const r = Math.random() * 16 | 0
    const v = c === 'x' ? r : (r & 0x3 | 0x8)
    return v.toString(16)
  })
}
