
/**
 * 积分抽奖：签到完成任务完成后去抽奖
 * @param data 积分抽奖接口
 * @param data.acCode 活动编码
 * @param data.activityPrizeId 奖品ID
 * @param data.appId 小程序appId
 * @param data.authCode 小程序appId对应获取的authcode
 * @returns
 */
export default function exchangeDraw (params: any = {}, options: any = {}) {
  return this.get('/app/activity/sign/exchange-draw', params, options)
}
