/**
* 活动奖品分享次数+1 /app/award/shareNum/add
* @ 使用场景:
用户分享成功一次,就调用一次该接口.该活动奖品分享次数＋1,返回总分享数.
* @param {object} data
* @param {number} data.activityPrizeId
* @param {object} options
* @returns
*/

export default function addShareNum (params: any = {}, options: any = {}) {
  return this.get('/app/award/shareNum/add', params, options)
}
