/**
   * 查询活动uiContent接口
   * @params {object} params
   * @params {string} params.uid
   * @params {string} params.acCode
   * @params {string} params.appId
   * @returns {any}
   */
export default function getUiContent (params: any = {}, options: Object = {}) {
  return this.get('/app/activity/getUiContent', params, options)
}
