/**
 * 事件任务冲鸭信息 /app/task/event/cyInfo
 * @param {object} data
 * @param {string} data.taskId
 * @param {string} data.lunaSessionId
 * @param {object} options
 * @returns
 */
export default function getTaskEventCyInfo (params: any, options: Object = {}) {
  return this.get('/app/task/event/cyInfo', params, options)
}
