/**
   * 查询用户信息
   * @param {object} params
   * @param {string} params.appid
   * @param {string} params.lunaSessionId
   * @param {string} params.authCode
   * @returns {any}
   */
export default function queryUserInfo (params: any = {}, options: Object = {}) {
  return this.get('/app/user/info', params, options)
}
