/**
   * 查询用户往期开奖记录接口 现聚宝盆活动用
   * @param {object} params
   * @param {string} params.acCode
   * @param {string} params.uid
   * @param {string} params.drawRuleId
   * @param {string} params.drawRuleType
   * @returns {any}
   */
export default function queryPrizeShuffleInfo (params: any = {}, options: Object = {}) {
  return this.get('/app/user/prize/shuffle/info', params, options)
}
