/**
* 获取用户指定条件下完成任务的数量 /app/task/getUserTaskFinshedCount
* @param {object} data
* @param {string} data.acCode
* @param {string} data.condition
* @param {string} data.authType
* @param {object} options
* @returns
*/
export default function getUserTaskFinshedCount (params: any, options: Object = {}) {
  return this.get('/app/task/getUserTaskFinshedCount', params, options)
}
