/**
   * 查询用户奖品列表 活动中滚动的大奖公示
   * @param {object} params
   * @param {string} params.acCode
   * @returns {object} res
   * @returns {array} res.result list集合
   */
export default function queryWinningPrizeInfo (params: any = {}, options: Object = {}) {
  return this.get('/app/statistics/winning-prize-info', params, options)
}
