/**
* 保存用户头像、昵称 /app/user/share-info
* @使用场景:用户未授权时拉取授权弹框,若用户同意授权,保存用户的头像和昵称.
* @param {object} data
* @param {string} data.authCode
* @param {string} data.appId
* @param {object} options
* @returns
*/
export default function saveUserShareInfo (params: any = {}, options: any = {}) {
  return this.get('/app/user/share-info', params, options)
}
