import TFy from './fy'
import TModules from '../modules'

type TProxy = typeof TModules & TFy

/**
 * fy实例
 * 调用fy[模块名][方法名]
 */
const getProxy = (fy: any) => {
  const proxy: TProxy = new Proxy((fy), {
    get (target, prop, receiver) {
      const modules = fy._modules

      if (typeof prop === 'string' && ['debug'].includes(prop)) {
        return target.store[prop]
      } else if (typeof prop === 'string' && prop.indexOf('_') === 0) {
        return undefined
      } else {
        for (const key in modules) {
          if (prop === key) {
            const module = modules[prop]
            const moduleProxy = new Proxy(module, {
              get (target, prop, receiver) {
                if (typeof target[prop] === 'function') {
                  return target[prop]
                } else {
                  return fy[prop]
                }
              },
              set (target, prop, value) {
                return false
              }
            })
            return moduleProxy
          }
        }
      }

      return target[prop]
    },
    set (target, prop, value) {
      if (prop === 'debug') {
        return Reflect.set(target.store, prop, value)
      }
      return false
    }
  })

  return proxy
}

export default getProxy
