import store from './store'
import { hosts } from './constants'
import { encrypt, md5ForFyDengHuo, md5ForXinghuo, getUUid } from '../utils/fyUtil'
import qs from '../utils/qs'
import deepClone from '../utils/deepClone'

const getBaseURL = (options) => {
  const env = options.env || store.env || 'prod'
  const host = options.host
  if (options.baseURL) return options.baseURL
  // console.log({ hosts, env, host })
  return hosts[env][host]
}

const parseUrl = (url = '') => {
  const index = url.indexOf('?')
  if (index === -1) return [url]

  const prev = url.slice(0, index)
  const search = url.slice(index + 1)

  return [prev, qs.parse(search)]
}

export const requestInterceptor = (config) => {
  const {
    appId,
    uid,
    lunaSessionId,
    // platform,
    debug,
  } = store

  const method = config.method.toUpperCase()

  if (debug) {
    console.group('请求' + config.url)
    console.log('url: ', config.url)
    console.log('入参: ')
    console.table(method === 'GET' ? config.params : deepClone(config.data))
    console.log('完整请求配置：')
    console.table(deepClone(config))
  }

  config.params = config.params || {}
  config.data = config.data || {}
  config.host = config.host || 'fy'

  config.baseURL = getBaseURL(config)

  // 处理url参数
  const parsed = parseUrl(config.url)
  if (parsed.length === 2) {
    config.url = parsed[0]
    config.params = {
      ...parsed[1],
      ...config.params,
    }
  }

  if (config.host === 'fy') {
    // 扶摇
    const data = method === 'POST' ? config.data : config.params

    data.appId = data.appId || appId
    if (uid) {
      data.uid = data.uid || uid
    }
    if (lunaSessionId) {
      data.lunaSessionId = data.lunaSessionId || lunaSessionId
    }

    // 扶摇接口，最后都加密
    const timestamp = Date.now() // 当前时间戳
    const nonce = getUUid() // uuid
    const keys = Object.keys(data).sort() // 按照参数key排序后，拼接参数值
    let dataMapString = ''
    for (const item of keys) {
      (data[item] !== undefined && data[item] !== null) && (dataMapString += data[item])
    }
    const sign = md5ForFyDengHuo(dataMapString, timestamp, nonce) // md5加密

    config.headers = {
      sign,
      timestamp,
      nonce,
      apiVersion: '1'
    }
  } else if (config.host === 'xh') {
    // 星火

    // POST请求加密
    if (config.encrypt && method === 'POST') {
      const data = config.data || {}
      const timestamp = Date.now() // 当前时间戳
      if (uid) {
        data.uid = data.uid || uid
      }
      const content = encrypt(data) || ''// aes加密
      const sign = md5ForXinghuo(content, timestamp) // 获取签名sign参

      const encryptData: any = {
        timestamp,
        content,
        sign
      }
      if (data.acCode) {
        encryptData.acCode = data.acCode
      }

      config.data = encryptData
      delete config.encrypt
    }
  }

  if (debug) {
    console.log('处理后的配置：')
    console.table(deepClone(config))
    console.groupEnd()
  }
  return config
}

export const responseInterceptor = (response) => {
  if (response.status === 200) {
    if (response.data && response.data.lunaSessionId) {
      store.lunaSessionId = response.data.lunaSessionId
    }
  }

  const { config } = response
  const { debug } = store
  if (debug) {
    console.group('请求响应' + config.url)
    console.log('请求响应：', response)
    console.table(response)
    if (response.status === 200 && response.data) {
      console.log('响应数据：')
      console.table(response.data)
    }
    console.groupEnd()
  }
  return response
}

export const errorInterceptor = (error) => {
  return error
}
